import React from 'react';
import axios from 'axios';


class TestData extends React.Component {
   
    constructor() {
        super();
        this.state = {
            cData: [],
        };
    }

    componentDidMount() {
        axios.get('https://services1.arcgis.com/ESMARspQHYMw9BZ9/arcgis/rest/services/CTY_DEC_2016_EN_NC/FeatureServer/0/query?where=1%3D1&outFields=CTY16NM&outSR=4326&f=json')
            .then(response => {
                this.setState({ cData: response.data.features });
                //console.log(response.data.features[0].attributes.CTY16NM);
            });
    }
    render() {
        let data_list = this.state.cData;
        return (
            <div>
                {
                    data_list.map((itm, k) => {
                        let county_list = data_list[k].attributes.CTY16NM;

                        return (
                            <div>
                                {
                                    <p key={itm}>{county_list}</p>
                                }
                            </div>
                        );
                    })
                }
            </div >
        );
    }
}
export default TestData;