import React from 'react';
import axios from 'axios';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';

class Statedata extends React.Component {
    constructor() {
        super();
        this.state = {
            stateData: [],
        };
    }

    componentDidMount() {
        axios.get('https://api.covid19india.org/state_district_wise.json')
            .then(response => {
                this.setState({ stateData: response.data });
            });
    }

    render() {

        let keys = Object.keys(this.state.stateData);
        keys.shift();

        return (
            <div className='row'>
                <div className='col-md-12'>
                    <Accordion>
                        {
                            keys.map((itm, k) => {
                                let districts = this.state.stateData[itm].districtData;
 
                                let total_confirmed = 0;
                                let total_active = 0;
                                let total_recovered = 0;
                                let total_death = 0;

                                var district_list = [];
                                var x = 0;
                                var ob = '';
                                for (x in districts) {
                                    total_confirmed += districts[x].confirmed;
                                    total_active += districts[x].active;
                                    total_recovered += districts[x].recovered;
                                    total_death += districts[x].deceased;
                                    ob = districts[x];
                                    ob['district_name'] = x;
                                    district_list.push(ob);
                                }
                                return (
                                    <Card key={k+1}>
                                        <Card.Header>
                                            <Accordion.Toggle as={Button} variant="dark p-2" eventKey={k+1}>
                                                <Badge variant='light p-2 mr-2' className='badge'>{itm}</Badge>
                                                <span className='btn-primary p-1 mr-2'>Total Cases / {total_confirmed}</span>
                                                <span className='btn-warning p-1 mr-2'>Active / {total_active}</span>
                                                <span className='btn-success p-1 mr-2'>Recovered / {total_recovered}</span>
                                                <span className='btn-danger p-1'>Death / {total_death}</span>
                                            </Accordion.Toggle>
                                        </Card.Header>
                                        <Accordion.Collapse eventKey={k+1}>
                                            <Card.Body>
                                                <table className='table table-bordered table-striped responsive'>
                                                    <thead>
                                                        <tr>
                                                            <td className='p-3 mb-2 bg-dark text-white font-weight-bold'>District</td>
                                                            <td className='p-3 mb-2 bg-primary text-white font-weight-bold'>Confirmed</td>
                                                            <td className='p-3 mb-2 bg-warning text-dark font-weight-bold'>Active</td>
                                                            <td className='p-3 mb-2 bg-success text-white font-weight-bold'>Recovered</td>
                                                            <td className='p-3 mb-2 bg-danger text-white font-weight-bold'>Death</td>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            district_list.map((itm, k) => {
                                                                return (
                                                                    <tr key={k}>
                                                                        <td>{itm.district_name}</td>
                                                                        <td>{itm.confirmed}</td>
                                                                        <td>{itm.active}</td>
                                                                        <td>{itm.recovered}</td>
                                                                        <td>{itm.deceased}</td>
                                                                    </tr>
                                                                );
                                                            })
                                                        }
                                                    </tbody>
                                                </table>
                                            </Card.Body>
                                        </Accordion.Collapse>
                                    </Card>
                                );
                            })
                        }
                    </Accordion>
                </div>
            </div>
        );
    }
}
export default Statedata;