import axios from 'axios';
import React from 'react';
import { Badge } from 'react-bootstrap';

class World extends React.Component {
    constructor() {
        super();
        this.state = {
            data: [],
        };
    }

    componentDidMount() {
        axios.get('https://corona.lmao.ninja/v2/countries')
            .then(response => {
                this.setState({ data: response.data });
            });
    }

    render() {
        return (
            <div className='row'>
                <div className='col-md-12'>
                    <table className='table table-bordered table-striped mt-1 responsive'>
                        <thead>
                            <tr>
                                <td className='p-3 mb-2 bg-dark text-white font-weight-bold'>Country</td>
                                <td className='p-3 mb-2 bg-primary text-white font-weight-bold'>Confirmed</td>
                                <td className='p-3 mb-2 bg-warning text-dark font-weight-bold'>Active</td>
                                <td className='p-3 mb-2 bg-success text-white font-weight-bold'>Recovered</td>
                                <td className='p-3 mb-2 bg-danger text-white font-weight-bold'>Death</td>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.data.map((itm, k) => {
                                    return (
                                        <tr key={k}>
                                            <td><Badge>{itm.country}</Badge>
                                                <img style={{ width: '64px', marginLeft: '10px' }} alt={itm.country} src={itm.countryInfo.flag} />
                                            </td>
                                            <td>{itm.cases}</td>
                                            <td>{itm.active}</td>
                                            <td>{itm.recovered}</td>
                                            <td>{itm.deaths}</td>
                                        </tr>
                                    );
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}
export default World;