import React from 'react';
import indiaFlag from './imgs/india-flag.jpg'
import Card from 'react-bootstrap/Card';
import Statedata from './Statedata';
import axios from 'axios';

class India extends React.Component {
    constructor() {
        super();
        this.state = {
            data: {},
        };
    }

    componentDidMount() {
        axios.get('https://corona.lmao.ninja/v2/countries/india')
            .then(response => {
                this.setState({ data: response.data })
            });
    }

    render() {
        return (
            <div className='row'>
                <div className='col-md-12 p-3'>
                    <img alt='India Flag' src={indiaFlag} />
                    <h4>INDIA</h4>
                </div>
                <div className='col-md-12'>
                    <div className='row'>
                        <div className='col-md-3'>
                            <Card className='badge badge-primary'>
                                <Card.Body>
                                    <Card.Title>TOTAL CASES</Card.Title>
                                    <h3>{this.state.data.cases}</h3>
                                    <Card.Text>
                                        Today / {this.state.data.todayCases}
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </div>
                        <div className='col-md-3'>
                            <Card className='badge badge-warning'>
                                <Card.Body>
                                    <Card.Title>ACTIVE CASES</Card.Title>
                                    <h3>{this.state.data.active}</h3>
                                    <Card.Text>
                                        <span>&nbsp;</span>
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </div>
                        <div className='col-md-3'>
                            <Card className='badge badge-success'>
                                <Card.Body>
                                    <Card.Title>RECOVERED</Card.Title>
                                    <h3>{this.state.data.recovered}</h3>
                                    <Card.Text>
                                        Today / {this.state.data.todayRecovered}
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </div>
                        <div className='col-md-3'>
                            <Card className='badge badge-danger'>
                                <Card.Body>
                                    <Card.Title>DEATH</Card.Title>
                                    <h3>{this.state.data.deaths}</h3>
                                    <Card.Text>
                                        Today / {this.state.data.todayDeaths}
                                    </Card.Text>
                                </Card.Body>
                            </Card>
                        </div>
                    </div>
                </div>

                <div className='col-md-12'>
                    <Statedata />
                </div>

            </div>
        );
    }
}
export default India;